.FAQPage {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 30px 64px;
  @include screen_705 {
    padding: 30px 16px;
  }
  @include screen_601 {
    padding: 0px 16px;
  }
  .FAQBody {
    margin-top: 40px;
    margin-bottom: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1366px;
    width: 100%;
    @include screen_705 {
      margin-top: 16px;
      margin-bottom: 50px;
      width: 100%;
    }

    .title {
      font-style: normal;
      font-weight: 700;
      font-size: 23px;
      line-height: 31px;
      color: #443988;
      margin-bottom: 0;
      @include screen_705 {
        font-size: 16px;
        line-height: 22px;
        min-width: 140px;
      }
    }

    .Questions {
      margin-top: 32px;
      background: #ffffff;
      box-shadow: 0px 0px 12px 2px rgba(0, 0, 0, 0.25);
      border-radius: 8px;
      .accordion {
        box-shadow: none !important;
        margin: 10px 72px;
        @include screen_705 {
          margin: 10px 16px;
        }
      }

      .questionTitle {
        font-family: "Open Sans" !important;
        font-weight: 600 !important;
        font-size: 13px !important;
        line-height: 20px !important;
        color: #878787 !important;
      }
      .answerText {
        font-family: "Open Sans" !important;
        font-weight: 400 !important;
        font-size: 13px !important;
        line-height: 20px !important;
        color: #878787 !important;
        a {
          text-decoration: none !important;
          color: #443988;
        }
      }
    }
  }

}

