.bannerLoginTop {
  width: 100%;
  margin-top: 18px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  @include screen_705 {
    display: none;
  }
  .banner {
    width: 100%;
    z-index: 98;
    min-height: 180px;
  }
  .container {
    position: absolute;
    z-index: 99;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      font-weight: 700;
      font-size: 15px;
      line-height: 20px;
      color: #ffffff;
      margin-top: 40px;
    }
  }
}
.loginContainer {
  margin-left: 72px;
  margin-right: 72px;
  margin-top: 32px;
  margin-bottom: 80px;
  @include screen_1366 {
    min-height: 45vh;
  }
  @include screen_705 {
    margin: 32px 0px 0px 0px;
  }
  @include screen_601 {
    margin: 0;
  }
  .loginBreadcrumbs {
    @include screen_705 {
      margin-left: 16px;
    }
  }
}

@import './boxLogin.scss';
@import './loginPopUp.scss';
