body {
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar-thumb {
    background: #babcbe;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
}

.Breadcrumbs {
  margin-left: 74px;
  margin-top: 56px;
  @include screen_768 {
    margin-left: 32px;
  }
  @include screen_705 {
    margin-left: 16px;
    margin-top: 16px;
  }
}
.TituloCartCheck {
  font-family: $fontFamily;
  font-size: 25px;
  font-weight: 700;
  color: #443988;
  margin-top: 0px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
.spinnerG {
  width: 36px;
  height: 36px;
  border: 5px solid rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  border-left-color: #7d47ff;
  animation: spin 1s ease infinite;
}
.spinnerM {
  width: 20px;
  height: 20px;
  border: 5px solid rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  border-left-color: #7d47ff;
  animation: spin 1s ease infinite;
}
.spinnerS {
  width: 10px;
  height: 10px;
  border: 5px solid rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  border-left-color: #7d47ff;
  animation: spin 1s ease infinite;
}
.spinnerWhite {
  border-left-color: #dbd2f0 !important;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.problemaMaxWidthCheckout {
  @include screen_1366 {
    max-width: 760px !important;
  }
}
.problemaMaxWidthCarrito {
  @include screen_1366 {
    max-width: 895px !important;
  }
}
.maxWidthResumeBox {
  @include screen_1366 {
    max-width: 296px !important;
  }
  @include screen_1001 {
    margin-left: 0px !important;
    margin-bottom: 32px !important;
  }
}

.errorBox {
  max-width: 672px;
  width: 100%;
  margin: 12px 0px;
  display: flex;
  align-items: center;
  background: #fdfeff;
  border: 1px solid #ff3f20;
  box-shadow: -10px -10px 30px rgba(223, 229, 239, 0.25),
    10px 10px 30px rgba(223, 229, 239, 0.25);
  border-radius: 6px;
  @include screen_705 {
    max-width: 370px;
    width: 100%;
  }
  .cruz {
    font-size: 24px;
    margin-left: 32px;
    margin-right: 24px;
    @include screen_389 {
      margin-left: 24px;
      margin-right: 16px;
    }
  }
  p {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #878787;
  }
}

.scrollFilter {
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-thumb {
    background: #babcbe;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
}

.botonAplicarFiltros {
  box-shadow: 0px 2px 6px rgba(66, 65, 67, 0.1) !important;
  color: white !important;
  padding: 4px 24px !important;
  display: flex !important;
  margin-bottom: 12px !important;
  &:hover {
    background-color: #443988 !important;
  }
}

.perfilVacio {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  div {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    img {
      width: 100%;
      max-width: 100px;
    }
    p {
      font-weight: 700;
      font-size: 18px;
      line-height: 25px;
      color: #424143;
    }
    button {
      border: 1px solid #443988;
      filter: drop-shadow(0px 0px 6px rgba(66, 65, 67, 0.1));
      border-radius: 20px;
      max-width: 200px;
      width: 100%;
    }
  }
}

.swal2-styled.swal2-confirm,
.swal2-styled.swal2-deny,
.swal2-styled.swal2-cancel {
  font-family: "Open Sans";
  font-weight: 700;
  font-size: 13px;
}

.containerSliders {
  margin: 0px 80px;
  @include screen_1190 {
    margin: 0px 60px;
  }
  @include screen_1001 {
    margin: 0px 50px;
  }
  @include screen_768 {
    margin: 0px 36px;
  }
  @include screen_601 {
    margin: 0px 24px;
  }
  @include screen_460 {
    margin: 0px 12px;
  }
  .react-multi-carousel-item {
    width: 264px !important;
    margin: 24px 22px;
    @include screen_768 {
      width: 220px !important;
    }
    @include screen_601 {
      width: 160px !important;
    }
  }
}

.image-gallery-thumbnails-container {
  .image-gallery-thumbnails {
    overflow-y: scroll !important;
    &::-webkit-scrollbar {
      width: 0px;
    }
  }
  button {
    border: none !important;
    img {
      filter: opacity(60%);
    }
  }
  .active {
    img {
      filter: opacity(100%);
    }
  }
}

.buscadorDisabled{
  color: #ff3f20 !important;
}
.roperosMobile{
  font-size: 14px;
  line-height: 19px;
  color: #FF3F20;
  padding-left: 16px;
  padding-top: 16px;
}

.returnLink {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-top: 24px;
  p {
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
  }
  img {
    height: 7.94px;
    width: 4.24px;
    margin-right: 10px;
  }
}